<script>
export default {
    name: 'TechDocs',
    data() {
        return {
            search: '',
            todos: [],
            currentPage: 1
        }
    },
    computed: {
        todosByTitle() {
        return this.todos.filter(item => item.keywords.toLowerCase().indexOf(this.search.toLowerCase()) !== -1)
        },
        todosByShow() {
        return this.todosByTitle.slice(0, this.currentPage * 7);
        },
        totalPages() {
        return Math.ceil( this.todosByTitle.length / 7);
        }
    },
    methods: {
        nextPage(){
        if(this.currentPage < this.totalPages) this.currentPage++
        },
        clearSearch(){
        this.search = '';
        this.currentPage = 1;
        }
    },
    beforeMount() {
        fetch('tech-document.json')
        .then(response => response.json())
        .then(json => {
            this.todos = json;
        })
  }
}
</script>

<style lang="sass" scoped>
.search
    display: flex
    flex-direction: row
    align-items: center
    justify-content: space-between
    padding: 0 15px 0 7px
    background-color: #fff
    border-radius: 5px
    border: 1px solid #9E9E9E
    width: 450px
    margin: 30px 0
    &__input
        padding: 10px 15px 10px 7px
        width: calc( 100% - 25px )
        font-size: 12px
        color: #9E9E9E
    &__icon
        width: 20px
        cursor: pointer
.template
  list-style: none
  margin: 0
  padding: 0
  margin-bottom: 50px
  &__block
    display: flex
    flex-direction: row
    align-items: center
    font-size: 14px
    width: 70%
    margin-bottom: 30px
  &-text
    display: flex
    flex-direction: column
    margin-left: 25px
    &__title
      margin-bottom: 10px
      font-weight: 700
      font-size: 14px
      line-height: 1.4
    &__description
      margin-bottom: 3px
      font-size: 12px
    &__tcakt
      font-size: 12px
      font-weight: 300

.close-icon
  position: relative
  transform: rotate(45deg)
</style>